import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(a,col,toggle): any {
    if(col)
    {
    	return a.sort((x,y)=>{
    		if(toggle)
    		{
    			if(x[col]<y[col]){return 1}	
    			else{return -1}
    			return 0

    		}
    		else
    		{
    			if(y[col]<x[col]){return 1}	
    			else{return -1}
    			return 0
    			
    		}
    	})
    }
    else
    {
    	return a
    }
  }

}
