import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  colName:string=""
  colValue:any=""
  a:todos[]=[]
  ob:todos
  m:string=""
  sortcol:string=""
  pp=5
  start=0
  pages:number[]=[]
  sortorder:boolean=false
  s:string=""
  constructor()
  {
  	if(localStorage.getItem("todos"))
  	{
  		this.a=JSON.parse(localStorage.todos)
      if(this.a.length>0)
      {
        let n=Math.ceil(this.a.length/this.pp)
        this.pages=new Array(n).fill(0).map((x,i)=>i+1)
      }
  	}
  }
  save()
  {
  	localStorage.setItem("todos",JSON.stringify(this.a))
  }
  insert(data)
  {
  		let todo=data
  		todo["date_added"]=Date.now()
      todo["status"]=0
  	if(this.a.length==0)
  	{
  		console.log(todo)
  		this.a.push(todo)
  		this.m="new todo created"
  	}	
  	else
  	{	
  		if(this.a.filter(item=>item.title==data.title).length>0)
  		{

	  		this.m="this todo already exists"
  		}	
  		else
  		{
	  		this.a.push(todo)
  		}
  	}
  	this.save()
  }
  edit(ob,i)
  {
    this.ob=ob
    this.ob.index=i
  }
  del(i)
  {
  		this.m="this todo deleted"
  		this.a.splice(i,1)
  		this.save()
  }
  update()
  {
    this.save()
  }
  status_update(status,i)
  {
    this.a[i].status=status
    this.save()
  }

  _sort(col)
  {
  this.sortcol=col
   this.sortorder =!this.sortorder
  }

  _paginate(x)
  {
    this.start=x
  } 
  howmany(a:any[],status:boolean):number
  {
    return a.filter(x=>x.status==status).length
  }
}


interface todos
{
  index?:number
	title:string
	content:string
	date_added:string
	status:boolean
}